.tokens-container{
    width: 90%;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 25% 75%;
    border-top:solid 1px  black;
    padding:20px 0  ;
    background: #A2A2A2;
    color: white;
    
    svg, img{
            height: 18px;
            width: 18px;

            @media screen and (max-width:1150px) {
                height: 10px;
            width: 10px;
            }

    }
    svg{
        transition: 0.2s;
        &:hover{
            color: aqua;
        }
    }
    
    

    .left-part{
        display: grid;
        grid-template-columns: 10% 10% 10% 70%;

        @media screen and (max-width:950px) {
            grid-template-columns: 15% 15% 10% 60%;
        }
        
        .staricon-container{
            display: flex;
            justify-content: center;

            @media screen and (max-width:950px) {
                font-size: 0.7rem;
            }
        }

        p{
            @media screen and (max-width:950px) {
                font-size: 0.7rem;
            }
        }

        p, .img, .info, .staricon-container{
            display: flex;
            align-items: center;
        }
        

        .info{
            display: flex;
            justify-content: start;           

            h2{
                font-size: 1.3rem;
                margin: 0 10px;

                @media screen and (max-width:1150px) {
                    font-size: 1rem;
                }

                span{
                    font-size: 0.6rem;
                    font-weight: 200;
                    color: rgba(255, 255, 255, 0.66);

                    @media screen and (max-width:1150px) {
                        font-size: 0.4rem;
                    }
                    @media screen and (max-width:950px) {
                        display: none;
                    }
                }
            }

            a{
                color: white;
                transition: 0.2s;
                

                &:hover{
                    transform: scale(1.05);
                    color: aqua;
                }
            }
        }
    }

    .right-part{
        display: grid;
        grid-template-columns: repeat(10, 10%);

        p, div{
            display: flex;
            align-items: center;
            font-size: 0.9rem;
            margin: 0 auto;

            @media screen and (max-width:1500px) {
                font-size: 0.7rem;
            }
            @media screen and (max-width:1150px) {
                font-size: 0.6rem;
            }
        }
    }
}